import { ApexOptions } from 'apexcharts';
import React, { useState } from 'react';
import ReactApexChart from 'react-apexcharts';
import { chart_config_area } from '../../../../shared/utility';

interface ChartOneState {
  series: {
    name: string;
    data: number[];
  }[];
}

export type ChartOneProps = {
  series: any;
  categories: any;
}

const ChartOne = (props: ChartOneProps) => {
  const { series, categories } = props;
  const [state, setState] = useState<ChartOneState>({
    series: series,
  });


  const options: ApexOptions = {
    ...chart_config_area,
    xaxis: {
      ...chart_config_area,
      categories: categories,
    }
  }

  const handleReset = () => {
    setState((prevState) => ({
      ...prevState,
    }));
  };
  handleReset;

  return (
    <div>
      <div id="chartOne" className="-ml-5">
        <ReactApexChart
          options={options}
          series={state.series}
          type="area"
          height={350}
        />
      </div>
    </div>

  );
};

export default ChartOne;
